const actions = {
  getPurchaseReturns: (page, search, filter) => ({
    type: "GET_PURCHASE_RETURNS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPurchaseReturn: () => ({
    type: "CREATE_PURCHASE_RETURN_REQUEST"
  }),
  getPurchaseReceipts: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_RECEIPTS_PT_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getPurchaseInvoices: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_INVOICES_PT_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storePurchaseReturn: data => ({
    type: "STORE_PURCHASE_RETURN_REQUEST",
    payload: { body: data }
  }),
  editPurchaseReturn: (id_purchase_return, search) => ({
    type: "EDIT_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return: id_purchase_return, search: search }
  }),
  updatePurchaseReturn: (id_purchase_return, data) => ({
    type: "UPDATE_PURCHASE_RETURN_REQUEST",
    payload: {
      id_purchase_return: id_purchase_return,
      body: data
    }
  }),
  showPurchaseReturn: id_purchase_return => ({
    type: "SHOW_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return: id_purchase_return }
  }),
  confirmPurchaseReturn: id_purchase_return => ({
    type: "CONFIRM_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return }
  }),
  unconfirmPurchaseReturn: id_purchase_return => ({
    type: "UNCONFIRM_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return }
  }),
  closePurchaseReturn: id_purchase_return => ({
    type: "CLOSE_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return }
  }),
  cancelPurchaseReturn: (id_purchase_return, data) => ({
    type: "CANCEL_PURCHASE_RETURN_REQUEST",
    payload: { id_purchase_return, body: data }
  }),
  uploadDocument: (id_purchase_return, data) => ({
    type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_purchase_return,
      body: data
    }
  }),
  downloadDocument: id_purchase_return_document => ({
    type: "DOWNLOAD_PURCHASE_RETURN_DOCUMENT_REQUEST",
    payload: { id_purchase_return_document }
  }),
  deleteDocument: (id_purchase_return_document, id_purchase_return) => ({
    type: "DELETE_PURCHASE_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_purchase_return_document: id_purchase_return_document,
      id_purchase_return: id_purchase_return
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_PURCHASE_RETURN",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
